<template>

    <f7-page :class="'navbar-fixed'">

        <navbar></navbar>

        <toolbar></toolbar>

        <div class="quran-head">

            <!--<div class="page-title"> lfsagsdg </div>-->

            <div class="sura-header">
                <div class="part-right">30</div>
                <div class="sura-name">
                    
                </div>
                <div class="part-left">129</div>
            </div>

        </div>


        <div class="page-content">

            <div class="quran-basmla">
                <a>
                       
                </a>
            </div>

            <div class="block quran-sura">

                    <span class="quran-aya" v-for="i in 40" @click="activate(i)" :class="{active: isActivated(i)}">
                           
                       <span class="quran-aya-separator"></span>
                        <span class="aya-num">12</span>
                    </span>
            </div>

        </div>



    </f7-page>

</template>

<style>


</style>

<script>

    import Vue from 'vue';

    export default {

        data() {
            return {
                active: 0
            }
        },


        methods: {
            activate(i){
                if(this.active == i){
                    this.active = 0;
                }else{
                    this.active = i;
                }

            },

            isActivated(i){
                return i == this.active;
            }
        },

        components: {
            navbar: require("./partials/Navbar.vue"),
            toolbar: require("./partials/Toolbar.vue")
        }
    }

</script>



